@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  align-items: center;
}


.home-container{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}
.img-background{
    background-image: url(/static/media/projects.1347f1c0.jpg);
    background-size: cover;
    width:60vw;
    height: 100vh;
    background-repeat: no-repeat;
}
.title-section{
    width:40vw;
    background-color: #2D364C ;
    height: 100vh;
    padding:2%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.title-text{
    color:#fff;
    font:Gotham Titles;
    margin:20px;
}
.auth-container{
    background-color: #181a22 ;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.row-app{
    display: flex;
    flex-direction: row;
  }
